<template>
	<div>
		<keep-alive>
			<Layout>
				<Sider hide-trigger>
					<Menu active-name="0" :open-names="['4']" style="width:auto" ref="menu">
						<Submenu name="4">
							<template slot="title">
								<Icon type="md-body" />ChatList
							</template>
							<MenuItem v-for="(persion,index) in chatList" :name="index.toString()" :key="persion.name" @click.native="change(index)">{{persion.name}}</MenuItem>
						</Submenu>
					</Menu>
				</Sider>
				<Layout>
					<Content>
						<div class="content">
							<div class="chat-container">
								<Chat v-if="visible" :participants="current" :myself="myself" :messages="currentMessage" :onType="onType"
								 :onMessageSubmit="onMessageSubmit" :chatTitle="chatTitle" :placeholder="placeholder" :colors="colors"
								 :borderStyle="borderStyle" :onClose="onClose" :hideCloseButton="hideCloseButton" :closeButtonIconSize="closeButtonIconSize"
								 :submitIconSize="submitIconSize" :asyncMode="asyncMode">
									<template v-slot:header>
										<div class="header-slot">
											<a v-for="(participant, index) in current" :key="index" class="custom-title">{{`${participant.name} `}}</a>
										</div>
									</template>
								</Chat>
							</div>
						</div>
					</Content>
				</Layout>
			</Layout>
		</keep-alive>
	</div>
</template>
<script>
	import eventBus from '../../eventBus/friendsBus'
	import {
		Chat
	} from "vue-quick-chat";
	import moment from "moment";
	import SockJS from "sockjs-client";
	import Stomp from "stompjs";
	export default {
		name: "app",
		components: {
			Chat
		},
		created() {
			console.log("...................................Chat Page is created ...................................");
		},
		mounted: function() {
			console.log("...................................Chat Page is mounted ...................................");
			this.fun1()
			this.initWebSocket(); //
			this.change(0); //0
		},
		beforeDestroy: function() {
			// ,
			console.log("beforeDestroy");
			this.disconnect();
			clearInterval(this.timer);
			//Bus
			//eventBus.$destroy()
			console.log('...................................Chat Page is beforeDestroyed ...................................')
		},
		data() {
			return {
				chatList: [{
					name: "zzj",
					id: 0
				}, {
					name: "skr",
					id: 1
				}], //id
				myself: {
					name: "myself",
					id: 1
				},
				current: [{
					name: "",
					id: 10
				}], //
				currentMessage: [], //
				messageList: [ //chatList
					[{
						content: "",
						myself: true,
						participantId: 1,
						timestamp: {
							year: 2012,
							month: 3,
							day: 5,
							hour: 20,
							minute: 10,
							second: 3,
							millisecond: 123
						},
						uploaded: true,
						viewed: true
					}, ],
					[{
						content: "",
						myself: true,
						participantId: 1,
						timestamp: {
							year: 2012,
							month: 3,
							day: 5,
							hour: 20,
							minute: 10,
							second: 3,
							millisecond: 123
						},
						uploaded: true,
						viewed: true
					}, ]
				],
				visible: true, //v-if 
				chatTitle: "My chat title", // 
				placeholder: "send your message", //
				colors: { //
					header: {
						bg: "#708090",
						text: "#fff"
					},
					message: {
						myself: {
							bg: "#fff",
							text: "#bdb8b8"
						},
						others: {
							bg: "#fb4141",
							text: "#fff"
						},
						messagesDisplay: {
							//bg: 'rgb(247, 243, 243)',
							//bg: '#f7f3f3'
							bg: "#f7f3f3"
						}
					},
					submitIcon: "#b91010" //
				},
				borderStyle: { //border 
					topLeft: "10px",
					topRight: "10px",
					bottomLeft: "10px",
					bottomRight: "10px"
				},
				hideCloseButton: false,
				submitIconSize: "40px",
				closeButtonIconSize: "20px",
				asyncMode: true
			};
		},
		methods: {
			change(index) { //
				this.current[0].name = this.chatList[index].name;
				this.current[0].id = this.chatList[index].id;
				this.currentMessage = this.messageList[index];
			},
			onType: function(e) { //
				console.log("typing");
			},
			onMessageSubmit(message) { //
				console.log(this.currentMessage);
				this.currentMessage.push(message);
				this.stompClient.send( //
					"/app/chat", {},
					JSON.stringify({
						'to': this.current[0].name, // 
						'content': message.content, // 
						'name': this.myself.name // 
					})
				);
				// timeout simulating the request
				setTimeout(() => { //2
					message.uploaded = true;
				}, 2000);
			},
			onClose() { //
				this.visible = false;
			},
			connect() { //socket
				this.socket = new SockJS("/chat");
				this.stompClient = Stomp.over(this.socket);
				this.stompClient.connect({}, (frame) => {
					//serConnected(true);
					// 
					// 
					// 
					this.stompClient.subscribe(
						"/user/queue/chat",
						(greeting) => {
							this.showGreeting(JSON.parse(greeting.body));
						},
						err => {
							// 
							console.log(err);
						}
					);
				});
			},
			initWebSocket() { //
				this.connect();
			},
			removeTab(targetName) { //
				console.log(targetName);
			},
			showGreeting(message) //,chatListchatList
			{
				console.log("showGreeting");
				for (var i = 0, len = this.chatList.length; i < len; i++) {
					if (i.name == message.name) {
						this.messageList[i].push({
							content: message.content,
							myself: false,
							participantId: i,
							timestamp: {
								year: 2014,
								month: 3,
								day: 5,
								hour: 20,
								minute: 10,
								second: 3,
								millisecond: 123
							},
							uploaded: true,
							viewed: true
						});
						break;
					}
					if (i == len - 1) {
						this.chatList.push({
							name: message.name,
							id: i,
						});
						this.messageList.push([{
							content: message.content,
							myself: false,
							participantId: i,
							timestamp: {
								year: 2014,
								month: 3,
								day: 5,
								hour: 20,
								minute: 10,
								second: 3,
								millisecond: 123
							},
							uploaded: true,
							viewed: true
						}]);
					}
				}
			},
			disconnect() { //
				if (this.stompClient !== null) {
					this.stompClient.disconnect();
				}
			},
			fun1(){
				console.log('******fun1******')
				var that = this
				
				eventBus.$on('wantToChat', function(val){
					//console.log('........................................'+val)
					var id = that.chatList.length
					that.chatList.push({
						name: val,
						id: id,
					})
					//console.log(this.$uid)
					console.log(that.chatList)
					
				})
				
			}
		}
	};
</script>

<style>
	#app {
		font-family: "Avenir", Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		margin-top: 60px;
	}

	.content {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-evenly;
		flex-wrap: wrap;
	}

	.chat-container {
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgb(247, 243, 243);
		padding: 10px 0 10px 0;
		height: 700px;
		width: 800px;
	}

	.external-controller {
		background: #2c3e50;
		height: 300px;
		width: 600px;
		display: flex;
		color: #eee;
	}

	.controller-btn-container {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 20px;
		padding-right: 20px;
		flex-wrap: wrap;
	}

	.btn-message {
		cursor: pointer;
		background: #eee;
		border: none;
		height: 40px;
		color: #2c3e50;
		border-radius: 5px;
		outline: none;
		transition: 0.3s;
	}

	.btn-participant {
		cursor: pointer;
		background: #eee;
		border: none;
		height: 40px;
		color: #2c3e50;
		border-radius: 5px;
		outline: none;
		transition: 0.3s;
	}

	.btn-message:hover {
		background: rgb(255, 255, 255);
	}

	.custom-title {
		color: #eee;
	}

	.header-slot {
		max-height: 100%;
	}
</style>
